/*    */

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;
  padding: 15px;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

/*     */

.modal {
  /*    */
  opacity: 0;
  visibility: hidden;

  /*      */
  width: 100%;
  max-width: 500px;
  @media screen and (min-width: $tablet) {
    width: 600px;
  }
  @media screen and (min-width: $desktop) {
    width: 800px;
  }

  /*     z-index */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30; /*      */

  /*      */
  box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  text-align: center;
  padding: 15px;
  border-radius: 3px;
  background-color: #fff;
  transition: 0.3s all;
}

.modal__iframe {
  width: 100%;
  height: 400px;
}

.modal__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;

  line-height: 1.17;
  margin-bottom: 15px;
  text-align: center;
  text-transform: uppercase;

  color: var(--accent-color);
}
.modal__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
}

/*        */

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

/*     */

.modal__cross {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #444;
  cursor: pointer;
}

.modal__cross:hover,
modal__cross:focus {
  fill: var(--accent-color);
}
