.advantages {
	padding-bottom: 95px;
	background-color: var(--secondary-background-color);
	@media screen and (min-width: $tablet){
		padding-bottom: 135px;
	}
}
.advantages__list {
	@media screen and (min-width: $tablet){
		width: 660px;
		display: flex;
		justify-content: space-between;
		// margin: -30px;
	}
	@media screen and (min-width: $desktop){
		width: 1044px;
	}
}

.advantages__container {
	@media screen and (min-width: $tablet) {
		display: flex;
		justify-content: center;
	}
}

.advantages__item:not(:last-child) {
	@media screen and (max-width: 767px){
		margin-bottom: 38px;
	}
	
}

.advantages__item {
	@media screen and (min-width: $tablet){
		flex-basis: calc(100% / 3 - 30px);

		// margin: 30px;
	}
	
}

.advantages__link {
	padding: 75px 10px 10px 10px;

	display: block;
	display: flex;
	flex-direction: column;
	border-radius: 15px;

	transition: transform var(--transition-duration) var(--timing-function), box-shadow var(--transition-duration) var(--timing-function), background-color var(--transition-duration) var(--timing-function);

	&:hover,
	&:focus {
		box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
		background-color: rgba(255,255,255,0.5);
		@media screen and (min-width: $tablet){
			transform: scale(1.15)
		}
	}
	@media screen and (min-width: $tablet){
		padding-top: 100px;
	}
}

.advantages__description {
	position: relative;
	margin-bottom: 10px;
	font-family: Titan One;
	font-style: normal;
	font-weight: normal;
	font-size: 36px;
	line-height: 1.13;
	letter-spacing: 0.04em;
	text-transform: uppercase;

color: var(--accent-color);
	@media screen and (min-width: $tablet){
		font-size: 48px;
		margin-bottom: 13px;
	}
	@media screen and (min-width: $desktop){
		font-size: 58px;
		margin-bottom: 15px;
	}
}

.advantages__description::before {
	content: "";
	display: inline-block;
	position: absolute;
	bottom: 52px;
	@media screen and (min-width: $tablet){
		bottom: 87px;
	}
	@media screen and (min-width: $desktop){
		bottom: 96px;
	}
}

.advantages__description--milk::before {
	width: 33px;
	height: 50px;
	background-image: url(../images/advantages-first.png);
	background-size: 33px 50px;

	@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
    background-image: url(../images/advantages-first@2x.png);
	}
}

.advantages__description--apple::before {
	width: 50px;
	height: 50px;
	background-image: url(../images/advantages-second.png);
	background-size: 50px 50px;

	@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
    background-image: url(../images/advantages-second@2x.png);
	}
}

.advantages__description--candy::before {
	width: 44px;
	height: 50px;
	background-image: url(../images/advantages-third.png);
	background-size: 44px 50px;

	@media screen and (min-device-pixel-ratio: 2),
  screen and (min-resolution: 192dpi),
  screen and (min-resolution: 2dppx) {
    background-image: url(../images/advantages-third@2x.png);
	}
}

.advantages__text {
	color: var(--primery-text-color);
font-weight: bold;
font-size: 12px;
line-height: 170%;
	@media screen and (min-width: $tablet) {
		font-size: 14px;
		line-height: 174%;
	}
	@media screen and (min-width: $desktop) {
		font-size: 16px;
		line-height: 176%;
	}
}

